}

.title-text {
  /* background-color: black; */
  font-size: 36px;
  width: inherit;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  -webkit-animation: flicker 1.5s infinite alternate;
